export default {
	props: {
		bookID:{
			type: Number,
			default: 0
		},
		bookImgUrl: {
			type: String,
			default: ''
		},
		bookName: {
			type: String,
			default: ''
		},
		bookSynopsis: {
			type: String,
			default: ''
		},
	},
	methods: {
		init() {
			this.showCancalCol(true);
			this.throwID(this.bookID)
		},
		
		throwID(id){
			this.$emit('getID', id)
		},
		showCancalCol(bol){
			this.$emit('showCancalCol', bol)
		}
	},
}
